import React from 'react'
import Layout from '../components/layout'
import SEO from "../components/seo"

export default () => (
  <Layout>
    <SEO title="Code of Conduct" />
    <h1>Code of Conduct</h1>

    <h2>Anti-harassment Policy</h2>
    <p>We want the Southeast Data Librarians Symposium to be a welcoming, supportive, and comfortable space for all participants, and we will work to make it a harassment-free environment. We do not tolerate harassment of symposium participants in any form.</p>
    <p>Harassment includes offensive verbal comments related to race, ethnicity, sex, gender identity or expression, sexual orientation, country of origin, age, disability, physical appearance, body size, religion, veteran status or other protected class status; discriminatory or offensive images in public spaces; intimidation; stalking; harassing or unwanted photography or recording; sustained disruption of talks or other events; inappropriate physical contact; and unwelcome sexual attention. Participants violating the anti-harassment policy may be warned or asked to leave by symposium organizers.</p>
    <p>Participants may discuss sensitive, discriminatory, offensive, or triggering topics in sessions if they are the subject of the session itself (e.g., a session focusing on the silencing of trans* history in institutional archives, or a session about workplace microaggressions experienced by people of color), but these subjects should be clear to participants prior to the start of the session.</p>

    <h2>Recording/Social Media Policy</h2>
    <p>We see the symposium as a place to share information, not only with other participants, but also with those who may not be able to attend. Sessions are, by default, considered open and can be written about on social media and photographed. If you do not want your picture taken, your words to be recorded or for either photographs or words to be attributed to you, please let other session participants know that. As a courtesy, please always ask prior to creating sound or video recordings of any sessions. Please be thoughtful and respectful with your photographs, recording, and sharing.</p>
    <p>Each session can decide to adopt different rules at the beginning of the session (e.g., completely private, or no attribution without asking first) as long as it informs all participants who join the session.</p>

    <h2>Reporting Harassment or Violations</h2>
    <p>If at any point during the symposium you feel uncomfortable or harassed, or if you witness any violations of these policies, please reach out to anyone who introduces themselves at the start of the day as a symposium organizer.</p>

    <h2>Acknowledgements</h2>
    <p>This policy is the same used at the <a href="https://mwdatalibrariansymposium.wordpress.com/code-of-conduct/">Midwest Data Librarian Symposium</a>. The MDLS policy is based on the <a href="https://nycarchivesunconference.wordpress.com/code-of-conduct/">NYC Archives Unconference Code of Conduct</a>, which is in turn based on the <a href="http://www2.archivists.org/statements/saa-code-of-conduct">SAA Code of Conduct</a> and the <a href="http://portland.adacamp.org/policies/#ahp">AdaCamp event policies</a>.</p>
  </Layout>
)